header {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: calc(10px + 2vmin);
}

header .logo-pokemon {
    position: relative;
    margin: 0 auto;
    font-family: 'Pokemon Solid', sans-serif;
    font-size: max(5vw, 5rem);
    color: #ffcb05;
    -webkit-text-stroke: 5px #3b5ca8;
    text-shadow: -1px 0px 0px #1d2c5e;
}